<template>
    <div class="globlgl  globlgl--custom">
        <div class="btns">
            <el-button class="btn" @click="handleBatchDelete">批量删除</el-button>
            <!-- <el-button class="btn" @click="blackImportDialogVisible = true">语音上传</el-button> -->
            <div class="search float-r">
             <el-input class="search-input" placeholder="号码" :maxlength="255" icon="search" v-model="searchInput" :on-icon-click="handleRecordCdrsSearch">
             </el-input>
               
            </div>
        </div>
        <div class="tables">
            <el-table class="table" ref="multipleTable" :data="recordCdrsData" border tooltip-effect="dark" @selection-change="handleSelectionChange">
                <el-table-column type="selection" width="55"></el-table-column>
                <el-table-column prop="vlanId" label="租户号"></el-table-column>
                <el-table-column prop="uuid" label="UUID">
                    <template slot-scope="scope">
                        <el-button @click="handlerecordCdrsCheck(scope.$index, scope.row)" type="text">{{scope.row.uuid}}</el-button>
                    </template>
                </el-table-column>
                <el-table-column prop="callerIdNumber" label="主叫号码"></el-table-column>
                <el-table-column prop="destinationNumber" label="被叫号码"></el-table-column>
                <el-table-column prop="recordSeconds" label="录音时长"></el-table-column>
                <el-table-column prop="fullRecordPath" label="录音文件">
                    <template slot-scope="scope">
                        <el-button @click="download(scope.$index, scope.row)" type="text">{{scope.row.fullRecordTitle}}</el-button>
                    </template>
                </el-table-column>
                <el-table-column prop="eventTime" label="时间" :formatter="dataFormat" width="180"></el-table-column>
                <el-table-column label="操作" width="120">
                    <template slot-scope="scope">
                        <el-button type="text" size="small" @click="handleRecordCdrsDelete(scope.$index, scope.row)">删除</el-button>
                    </template>
                </el-table-column>
            </el-table>
            <!--查看弹窗-->
            <el-dialog title="查看录音文件" class="w-500" :visible.sync="dialogFormCheck" :close-on-click-modal="2==1">
                <div class="item">租户号: {{showCheck.vlanId}}</div>
                <div class="item">主叫号码: {{showCheck.callerIdNumber}}</div>
                <div class="item">被叫号码: {{showCheck.destinationNumber}}</div>
                <div class="item">时间: {{showCheck.eventTime}}</div>
                <div class="item">录音时长: {{showCheck.recordSeconds}}</div>
                <div class="item">更新时间: {{showCheck.updateAt}}</div>
                <div class="item">创建时间: {{showCheck.createAt}}</div>
                <div class="item">录音文件路径: {{showCheck.fullRecordPath}}</div>
                <div slot="footer" class="dialog-footer">
                <el-button @click="dialogFormCheck = false">取 消</el-button>
                <el-button type="primary" @click="dialogFormCheck=false">确 定</el-button>
                </div>
            </el-dialog>
            <el-dialog title="导入" class="w-500 dialog--import-file" :visible.sync="blackImportDialogVisible" :close-on-click-modal="2==1">
                <import-file-form 
                    :importAPI="blackImportListFileAPI" 
                    :setUploadParams="setUploadParams" 
                    :dialogParent="blackImportDialogVisible"
                    @cancel="blackImportDialogVisible = false" 
                    @failedCallback="importfailedCallback"
                    @successCallback="importSuccessCallback">
                </import-file-form>
            </el-dialog>
        </div>
        <div class="float-r Pagination" style="text-align: left;margin-top: 10px;">
        <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="currentPage" :page-size="10" layout="total, prev, pager, next" :total="count">
        </el-pagination>
        </div>
    </div>
</template>

<script>

  import {
    recordCdrsList,
    deleterecordCdrsByIds,
    blackImportListFileAPI,
  }from '@/api/getData'
  import ElTableTreeColumn from '../base/table-tree/table-column.js'
  import importFileForm from '../call/importFileForm.vue'
  export default {
    data() {
      var checkNumber = (rule, value, callback) => { //必须填而且必须是数字
            if (value == undefined || "" === value) {
                callback(new Error('此处不能为空'));
            }
            if (!Number.isInteger(value)) {
                callback(new Error('请输入数字值'));
            } else {
                callback();
            }
        };
        var checkNull = (rule, value, callback) => { //要么不填，要填必须是数字
            if (value == undefined || "" === value) {
                callback();
            }
            if (!Number.isInteger(value)) {
                callback(new Error('请输入数字值'));
            } else {
                callback();
            }
        };
        var checkDateVule = (rule, value, callback) => { //必须填入日期
            if (value == undefined || "" === value) {
                callback(new Error('请输入日期'));
            }else{
                 callback();
            }
          
        };
      return {
        //分页
        currentRow: null,
        limit: 10,
        count: 0,
        currentPage: 1,
        searchInput: '', //搜索框
        recordCdrsData: [],
        showCheck: {}, //查看对象
        dialogFormCheck: false, //查看弹窗

        multipleSelection: [],

        formLabelWidth: '80px',
        formRules: {
           recordSeconds: [{validator: checkNull, trigger: 'blur'}],
         
        },
        blackImportDialogVisible: false,
        blackImportListFileAPI: blackImportListFileAPI,
      }
    },
    created() {
      this.getrecordCdrsList(); //加载数据
    },
    components: {
      ElTableTreeColumn,
      importFileForm: importFileForm,
    },
    methods: {
      //获取分页 总条数
      async getrecordCdrsList() {
        try {
          let query = {
            callerIdNumber: this.searchInput,
            type: 1,//表示录音文件
          }
          //分页
          const res = await recordCdrsList({
            pageindex:this.currentPage,
            pagesize:this.limit,
            query:JSON.stringify(query)
          });
          if (res.resultCode == '0') {
            this.recordCdrsData = [];
            //防止后台数据幅值，，前台页面只需要改动此处就好，还有一个好处 就是只选择我需要的数据
            this.showCheck = res.result.data;
            this.count=res.result.total;
            res.result.data.forEach(item => {
              let temp = item.fullRecordPath;
              if(temp == '-1'){
                temp = '文件缺失';
              }else{
                temp = '点击下载';
              }
              const tableItem = {
                callerIdNumber:item.callerIdNumber,
                createAt:item.createAt,
                destinationNumber:item.destinationNumber,
                eventTime:item.eventTime,
                fullRecordTitle:temp,
                fullRecordPath:item.fullRecordPath,
                recordSeconds:item.recordSeconds,
                updateAt:item.updateAt,
                uuid:item.uuid,
                vlanId:item.vlanId,
              }
              this.recordCdrsData.push(tableItem)
            })
          } else {
            throw new Error(res.message)
          }
        } catch (err) {
          console.log('获取数据失败', err);
        }
      },
     
      handleSizeChange(val) {
      },
      handleCurrentChange(val) {
        this.currentPage = val;
        this.getrecordCdrsList();
      },
      handleSelectionChange(val) {
        this.multipleSelection = val;
      },
      handleRecordCdrsSearch() {
        this.getrecordCdrsList();
      },
      formatter(row, column) {
        return ' -- ' + row.name;
      },
      dataFormat(row, column) {
         var time =  row[column.property];
         if(time != undefined && time != ''){
            return this.$moment(time).format("YYYY-MM-DD HH:mm:ss");   
         }else{
            return '';
         }
      },
      //批量删除
      handleBatchDelete() {
        let that = this;
        if (this.multipleSelection.length === 0) {
          this.$alert('请至少选择一条记录', '提示', {
            confirmButtonText: '确定',
            callback: () => {
              return false;
            }
          });
        } else {
          this.$confirm('此操作将永久删除记录, 是否继续?', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
          }).then(async() => {
            try {
              let deletedIdArr = [];
              that.multipleSelection.forEach(s => {
                deletedIdArr.push(s.uuid)
              })
              let deletedIdsString = deletedIdArr.join(',');
              let data = {
                uuid: deletedIdsString
              }
              //此方法可以和后台协商一致，单条与批量删除使用同一个函数
              const result = await deleterecordCdrsByIds(data);
              if (result.resultCode == '0') {
                that.getrecordCdrsList();
                this.$message({
                  type: 'success',
                  message: '删除成功!'
                });
              }
            } catch (err) {
              console.log(err)
            }
          }).catch(() => {
            this.$message({
              type: 'info',
              message: '已取消删除'
            });
          });
        }
      },
      //删除当前记录
      handleRecordCdrsDelete(index, row) {
        let that = this;
        this.$confirm('此操作将永久删除记录, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(async() => {
          try {
            let deletedIdsString = [row.uuid].join(',');
            let data = {
              uuid: deletedIdsString
            }
            //此方法可以和后台协商一致，单条与批量删除使用同一个函数
            const result = await deleterecordCdrsByIds(data);
            if (result.resultCode == '0') {
              that.getrecordCdrsList();
              this.$message({
                type: 'success',
                message: '删除成功!'
              });
            }
          } catch (err) {
            console.log(err)
          }
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });
        });
      },
      //查看
      handlerecordCdrsCheck(index, row) {
        this.dialogFormCheck = true;
        row.createAt=this.dateFormat(row.createAt);
        row.updateAt=this.dateFormat(row.updateAt);
        this.$data.showCheck = row;
      },
      dateFormat(time) {
         if(time != undefined && time != ''){
            return this.$moment(time).format("YYYY-MM-DD HH:mm:ss");
         }else{
            return '';
         }
         
      },
       //下载录音
      download(index, row) {
        if(row.fullRecordPath != -1){
           window.open("/ucc/virtual/download?type=fullpath&path="+row.fullRecordPath, '_blank');
        }
      },
      importSuccessCallback (msg) {
        console.log(msg);
        //this.getBlackList();
         this.$message({
            type: 'success',
            message: '导入成功'
          });
      },
      importfailedCallback(state,msg){
            this.$message({
                type: 'error',
                message: msg
            });
      },
      // 设置导入黑名单参数
      setUploadParams(importUploadFile) {
        return {
          filedata: importUploadFile.name,
        };
      },
    }
  }

</script>
<style scope lang="less">

  .globlgl {
    padding: 33px 0 0 26px;
    box-sizing: border-box;
    .float-l {
      float: left;
    }
    .float-r {
      float: right;
    }
    .m-t-25 {
      margin-top: 25px;
    }
    //table 改组建
    .el-table {
      width: 100%;
      height: 100%;
      border: 0;
      &:before,
      &:after {
        display: none;
      }
      .el-table__body-wrapper {
        overflow: hidden;
      }
      th,
      td,
      .cell {
        border: 0px;
        background-color: initial;
        padding: 0px;
      }
      .cell {
        padding-left: 10px;
      }
      tr {
        border-bottom: 1px solid #e6e6e6;
      }
      thead tr {
        border-bottom: 2px solid #e6e6e6;
      }
    }
    .w-500 {
      .el-dialog {
        width: 500px;
        .el-dialog__body {
          max-height: 500px;
          overflow-y: auto;
          .item{
            height:40px;
            line-height:40px;
          }
        }
      }
    }
    .w-1000 {
      .el-dialog {
        width: 1000px;
        .el-dialog__body {
          max-height: 500px;
          overflow-y: auto;
        }
      }
    }
    .el-button--text {
      color: #00a8d7;
    }
    .btns {
      .btn {
        width: 80px;
        height: 36px;
        border: 1px solid #e6e6e6;
        color: #454545;
      }
      .float-r {
        float: right;
      }
    }
    .tables {
      .table {
        border: 0;
        margin: 15px 0;
      }
    }
    .m-t-26 {
      margin-top: 26px;
    }
    .globlgl-type {
      margin-right: 20px;
    }
    .nav {
      color: #99a9bf;
      cursor: pointer;
      transition: .3s;
      vertical-align: baseline;
      margin-right: 30px;
      &:hover {
        color: #48576a;
      }
    }
    .tree-grid {
      margin: 15px 0;
    }
  }

</style>
<style lang="less">
    .globlgl.globlgl--custom .el-table .el-table__body-wrapper {
        overflow: auto;
    }
</style>



